export default {
    methods: {
        // 
        deleteNode(location) {
            if (this.selectedNode) {
                // Avoid error: `Cannot set property 'loc' of undefined`
                this.selectedNode.loc = null
                this.selectedNode.type = null
            }
            
            let nodeObj
            this.getNodeObj({
                location,
                callback: (nodeArray, index) => {
                    nodeObj = nodeArray[index]
                    // 
                    nodeArray.splice(index, 1)
                }
            })
            return nodeObj
        }
    }
}