<script>
export default {
    inject: ["note"],
    computed: {
        // 
        getThisObj() {
            if (!this.location) {
                return
            }

            let obj
            this.getNodeObj({
                location: this.location,
                callback: (nodeArray, index) => {
                    obj = nodeArray[index]
                }
            })
            return obj
        }
    },
    methods: {
        //   
        getNodeObj({
            parentObj=this.note,
            location,
            num=0,
            callback
        }) {
            if (num == location.length - 1) {
                //    index
                if (callback) {
                    callback(parentObj.CTS, location[num])
                    //       nodeArray      index
                    // nodeArray[index] 
                }
            }
            else {
                this.getNodeObj({
                    parentObj: parentObj.CTS[location[num]],
                    location: location,
                    num: num + 1,
                    callback: callback
                })
            }
        }
    }
}
</script>
