<script>
import { h } from "vue"
import getNodeObj from "../mixin/getNodeObj"
import compiler from "../../common/compiler"
import EventBus from "../../common/EventBus"

export default {
    data() {
        return {
            selected: false,
            initialContent: "",
            editing: false,
            dbTouch: false,
        }
    },
    inject: ["selectedNode"],
    mixins: [getNodeObj],
    props: [
        "tagName", "content",
        "location", "color",
    ],
    watch: {
        content(newVal) {
            this.$nextTick(() => {
                const HTMLOutput = compiler.outputer(newVal)
                this.$el.innerHTML = HTMLOutput
            })
        }
    },
    methods: {
        // 
        selectEvent() {
            if (this.selected) {
                // 
                EventBus.emit("fixedComponents-close")
                // 
                this.selectedNode.loc = null
                this.selectedNode.vnode = null
                this.selectedNode.type = null
                this.selected = false
            } else {
                // 
                if (this.selectedNode.loc) {
                    // 
                    this.selectedNode.vnode.selected = false
                }
                this.selectedNode.loc = this.location
                this.selectedNode.vnode = this
                this.selectedNode.type = this.tagName
                this.selected = true
            }
        }
    },
    render() {
        return h(this.tagName, {
            contentEditable: true,
            class: {
                "selected": this.selected,
                "editing": this.editing,
                "inline-style": true
            },
            style: {
                "color": this.color
            },
            onContextmenu: (e) => {
                e.preventDefault()
                this.selectEvent()
            },
            onTouchstart: () => {
                // 
                if (this.dbTouch) {
                    this.selectEvent()
                }
                this.dbTouch = true
                setTimeout(() => {
                    this.dbTouch = false
                }, 300)
            },
            onClick: (e) => {
                // 
                if (!this.editing) {
                    this.initialContent = e.target.innerText
                }
                this.editing = true
            },
            onBlur: (e) => {
                const resultContent = e.target.innerText
                // 
                if (resultContent != this.initialContent) {
                    //  this.note 
                    this.getThisObj.CT = resultContent
                    EventBus.emit("add-history", {
                        loc: this.location,
                        prop: "CT",
                        before: this.initialContent,
                        after: resultContent
                    })
                    this.initialContent = resultContent
                }
                this.editing = false
            }
        }, this.content)
    }
}
</script>